@-webkit-keyframes buttonFade {
  0% {
    background-color: white;
    top: -1px;
    -webkit-box-shadow: 0px 1px black;
            box-shadow: 0px 1px black;
  }
  60%{
    op: -2px;
    -webkit-box-shadow: 0.5px 2px black;
            box-shadow: 0.5px 2px black;
  }
  100% {
    op: -3px;
    background-color: purple;
    -webkit-box-shadow: 1px 4px black;
            box-shadow: 1px 4px black;
  }
}

@keyframes buttonFade {
  0% {
    background-color: white;
    top: -1px;
    -webkit-box-shadow: 0px 1px black;
            box-shadow: 0px 1px black;
  }
  60%{
    op: -2px;
    -webkit-box-shadow: 0.5px 2px black;
            box-shadow: 0.5px 2px black;
  }
  100% {
    op: -3px;
    background-color: purple;
    -webkit-box-shadow: 1px 4px black;
            box-shadow: 1px 4px black;
  }
}

@@keyframes buttonClick {
  0% {
    margin-top: 0px;
    -webkit-box-shadow: 0px 3px black;
            box-shadow: 0px 3px black;
  }
  50%{
    margin-top: 3px;
    -webkit-box-shadow: 0px 0px black;
            box-shadow: 0px 0px black;
  }
  100% {
    margin-top: 0px;
    -webkit-box-shadow: 0px 3px black;
            box-shadow: 0px 3px black;
  }
}

.sidebar {
  postion: absolute;
  display: inline-block;
  width: 225px;
  height: 70%;
  margin-left: 12%;
}

.sidebar-visable {
  width: 100%;
  height: 100%;
  margin-top: 90px;
}

.sidebar-hidden {
  visibility: hidden;
}

.sidebar-button {
  position: relative;
  width: 100%;
  height: 55px;
  background-color: white;
  font-size: 1.5rem;
  border: 1;
  border-radius: 4px;
}

.sidebar-button:focus {
  top: -4px;
  -webkit-animation: buttonFade 0.4s;
          animation: buttonFade 0.4s;
  -webkit-box-shadow: 1px 4px black;
          box-shadow: 1px 4px black;
  background-color: purple;
  outline:0;
}

.sidebar-button:active {
  -webkit-animation: buttonClick 1s;
          animation: buttonClick 1s;
  background-color: purple;
}



